<template>
    <div class="upload clearfix">
        <input @change="fileChange($event)" type="file" id="upload_file" name="upload_file" multiple style="display: none"/>
        <!--图片预览列表-->
        <ul class="imgList" >
            <li v-for="(url, index) in imgList" :key="index">
                <span class="delImgIcon" @click="delImg(index)"><svg-icon icon-class="删除" class="icon" /></span>
                <img :src="url.file.src" class="img">
            </li>
            <!--添加图片-->
            <li class="addImg" @click="chooseType">
                <span class="addImgIcon"><svg-icon icon-class="addImg" class="icon" /></span>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "uploadImg",
        data() {
            return {
                imgList: [],
                size: 0,
                limit: 10000, //限制图片上传的数量
                maxLimit: 10000
            }
        },
        methods: {
            chooseType() {
                document.getElementById('upload_file').click();
            },
            fileChange(el) {
                if (!el.target.files[0].size) return;
                this.fileList(el.target);
                el.target.value = ''
            },
            fileList(fileList) {
                let files = fileList.files;
                for (let i = 0; i < files.length; i++) {
                    //判断是否为文件夹
                    if (files[i].type != '') {
                        this.fileAdd(files[i]);
                    } else {
                        //文件夹处理
                        try {
                            this.folders(fileList.items[i]);
                        } catch (error) {
                            console.log(error);
                        }
                    }
                }
            },
            //文件夹处理
            folders(files) {
                let _this = this;
                //判断是否为原生file
                if (files.kind) {
                    files = files.webkitGetAsEntry();
                }
                files.createReader().readEntries(function (file) {
                    for (let i = 0; i < file.length; i++) {
                        if (file[i].isFile) {
                            _this.foldersAdd(file[i]);
                        } else {
                            _this.folders(file[i]);
                        }
                    }
                });
            },
            foldersAdd(entry) {
                let _this = this;
                entry.file(function (file) {
                    _this.fileAdd(file)
                })
            },
            // 判断是否出现滚动条
            hasScrollbar() {
                return document.body.scrollHeight > window.innerHeight;
            },
            fileAdd(file) {
                if (this.limit !== undefined) this.limit--;
                if (this.limit !== undefined && this.limit < 0) return;
                //总大小
                this.size = this.size + file.size;
                //判断是否为图片文件
                if (file.type.indexOf('image') == -1) {
                    this.$MessageBox('提示', '请选择图片文件！');
                } else {
                    let reader = new FileReader();
                    let image = new Image();
                    let _this = this;
                    reader.readAsDataURL(file);
                    reader.onload = function () {
                        file.src = this.result;
                        image.onload = function(){
                            let width = image.width;
                            let height = image.height;
                            file.width = width;
                            file.height = height;
                            _this.imgList.push({
                                file
                            });
                            _this.$nextTick(function () {
                                // DOM 现在更新了
                                // 若出现滚动条，整个页面往下移动些，否则的话，就看不到添加图片按钮了
                                if (_this.hasScrollbar()) {
                                    window.scrollTo(0, document.documentElement.clientHeight);
                                }
                            });
                        };
                        image.src= file.src;
                    }
                }
            },
            delImg(index) {
                this.size = this.size - this.imgList[index].file.size;//总大小
                this.imgList.splice(index, 1);
                if (this.limit !== undefined) {
                    this.limit = this.maxLimit -this.imgList.length;
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "~@/styles/theme.scss";

    .clearfix:after,
    .clearfix:after {
        content:".";
        display:block;
        height:0;
        clear:both;
        visibility:hidden;
    }

    ul,
    li {
        padding: 0;
        margin: 0;
        list-style: none;
    }

    .upload {
        margin-bottom: 10px;
        padding-bottom: 10px;
        .imgList {
            display: flex;
            flex-flow: row wrap;

            li {
                position: relative;

                width: 180px;
                height: 180px;
                margin-right: 10px;
                margin-bottom: 10px;

                .delImgIcon {
                    position: absolute;
                    width: 18px;
                    top: 10px;
                    right: 10px;
                    z-index: 999;
                    cursor: pointer;
                }
                .img {
                    width: 100%;
                    height: 100%;;
                }

                &.addImg {
                    text-align: center;
                    background: #F9FAFC;
                    border: 1px solid #C0CCDA;
                    display: flex;
                    cursor: pointer;
                    align-items: center;
                    justify-content: center;
                    .addImgIcon {
                        width: 48px;
                        height: 48px;
                    }
                }
            }
        }
    }

</style>
